
import { defineComponent } from 'vue';
import { mapGetters, mapActions } from 'vuex'


export default defineComponent({
  name: 'Death Picker',
  components: {
  },
  computed: {
    ...mapGetters([
      'deathPicks',
    ])
  },
  data() {
    return { 
    }
  },
  methods: {
    ...mapActions([
      'makeDeathPick'
    ]),
    makeSelection(playerId: number){
      this.makeDeathPick(playerId)
    }
  }
});
